import './App.css';
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import { Route, Routes, Navigate } from 'react-router-dom';
import { v4 as uuidV4 } from "uuid";
import theme from "assets/theme";
import Author from 'pages/Author';
import CV from 'pages/CV/CV';
import Projects from 'pages/Projects/Projects';
import RBA from 'pages/Projects/Scientific/RBA';
import Classify from "pages/Projects/Personal/Classify";
import GhostWriter from 'pages/Projects/Personal/GhostWriter';
import { useEffect, useState } from 'react';

function GhostRedirect() {
  const [redirectPath, setRedirectPath] = useState(null);

  useEffect(() => {
    // Generate a random ID when the component mounts
    const randomId = uuidV4();
    setRedirectPath(`/projects/ghost/${randomId}`);
  }, []);

  if (redirectPath) {
    return <Navigate to={redirectPath} replace />;
  }

  return null;
}

function App() {
  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Routes>
          <Route path="/" element={<Author />} />
          <Route path="/cv" element={<CV />} />
          <Route path="/projects" element={<Projects />} />
          <Route path="/projects/rba" element={<RBA />} />
          <Route path="/projects/classify" element={<Classify />} />
          <Route path="/projects/ghost/" element={<GhostRedirect />} />
          <Route path="/projects/ghost/:id" element={<GhostWriter />} />
        </Routes>
      </ThemeProvider>
    </div>
  );
}

export default App;
