import axios from 'axios';

import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import CircularProgress from '@mui/material/CircularProgress';

import MKBox from "components/MKBox";
import MKInput from "components/MKInput";
import MKButton from "components/MKButton";
import MKTypography from "components/MKTypography";

// Images
import bgImage from "assets/images/ai2.jpeg";
import { useRef, useState } from "react";
import React from "react";

function Contact() {
  const sendMessage = async () => {
    // Check if any of the form fields are empty
    if (!nameRef.current.value || !messageRef.current.value || !contactRef.current.value) {
      alert('Please fill out the fields marked with a star');
      return; // Exit early if form data is empty
    }
    const formData = new FormData();
    formData.append('name', nameRef.current.value);
    formData.append('concern', concernRef.current.value);
    formData.append('message', messageRef.current.value);
    formData.append('contact', contactRef.current.value);
    try {
      setLoading(true)
      const response = await axios.post('http://127.0.0.1:5000/contact', formData);
      if (response.status === 200) {
        setSentMessage(true)
        setError(false)
      }
    } catch (error) {
      console.error('Error sending contact message:', error);
      setError(true);

    } finally {
      setLoading(false); // Set loading back to false when request completes (success or error)
    }

  };
  const nameRef = useRef();
  const concernRef = useRef();
  const messageRef = useRef();
  const contactRef = useRef();

  const [sentMessage, setSentMessage] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  return (
    <MKBox component="section" py={{ xs: 0, lg: 6 }}>
      <Container>
        <Grid container item>
          <MKBox
            width="100%"
            bgColor="white"
            borderRadius="xl"
            shadow="xl"
            mb={6}
            sx={{ overflow: "hidden" }}
          >
            <Grid container spacing={2}>
              <Grid
                item
                xs={12}
                lg={5}
                position="relative"
                px={0}
                sx={{
                  backgroundImage: ({
                    palette: { gradients },
                    functions: { rgba, linearGradient },
                  }) =>
                    `${linearGradient(
                      rgba(gradients.dark.main, 0.8),
                      rgba(gradients.dark.state, 0.8)
                    )}, url(${bgImage})`,
                  backgroundSize: "cover",
                }}
              >
                <MKBox
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                  width="100%"
                  height="100%"
                >
                  <MKBox py={6} pr={6} pl={{ xs: 6, sm: 12 }} my="auto">
                    <MKTypography variant="h3" color="white" mb={1}>
                      Contact Information
                    </MKTypography>
                    <MKTypography variant="body2" color="white" opacity={0.8} mb={3}>
                      Fill up the form and I will get to you as soon as possible.
                    </MKTypography>
                    <MKBox display="flex" p={1}>
                      <MKTypography variant="button" color="white">
                        <i className="fas fa-phone" />
                      </MKTypography>
                      <MKTypography
                        component="span"
                        variant="button"
                        color="white"
                        opacity={0.8}
                        ml={2}
                        fontWeight="regular"
                      >
                        (+49) 174 6142597
                      </MKTypography>
                    </MKBox>
                    <MKBox display="flex" color="white" p={1}>
                      <MKTypography variant="button" color="white">
                        <i className="fas fa-envelope" />
                      </MKTypography>
                      <MKTypography
                        component="span"
                        variant="button"
                        color="white"
                        opacity={0.8}
                        ml={2}
                        fontWeight="regular"
                      >
                        nazarenuseric@gmail.com
                      </MKTypography>
                    </MKBox>
                    <MKBox display="flex" color="white" p={1}>
                      <MKTypography variant="button" color="white">
                        <i className="fas fa-map-marker-alt" />
                      </MKTypography>
                      <MKTypography
                        component="span"
                        variant="button"
                        color="white"
                        opacity={0.8}
                        ml={2}
                        fontWeight="regular"
                      >
                        Wankheimer Täle 9, 72072 Tübingen
                      </MKTypography>
                    </MKBox>
                  </MKBox>
                </MKBox>
              </Grid>
              <Grid item xs={12} lg={7}>
                <MKBox component="form" p={2} method="post">
                  <MKBox px={3} py={{ xs: 2, sm: 6 }}>
                    <MKTypography variant="h2" mb={1}>
                      Say Hi!
                    </MKTypography>
                    <MKTypography variant="body1" color="text" mb={2}>
                      I&apos;d be happy to hear from you.
                    </MKTypography>
                  </MKBox>
                  <MKBox pt={0.5} pb={3} px={3}>
                    <Grid container>
                      <Grid item xs={12} pr={1} mb={6}>
                        <MKInput
                          variant="standard"
                          label="My name is*"
                          placeholder="Full Name"
                          inputRef={nameRef}
                          InputLabelProps={{ shrink: true }}
                          fullWidth
                        />
                      </Grid>
                      <Grid item xs={12} pr={1} mb={6}>
                        <MKInput
                          variant="standard"
                          label="My concern is"
                          placeholder="Deeply concerning..."
                          inputRef={concernRef}
                          InputLabelProps={{ shrink: true }}
                          fullWidth
                        />
                      </Grid>
                      <Grid item xs={12} pr={1} mb={6}>
                        <MKInput
                          variant="standard"
                          label="Your message*"
                          placeholder="I want to say that..."
                          InputLabelProps={{ shrink: true }}
                          inputRef={messageRef}
                          fullWidth
                          multiline
                          rows={2}
                        />
                      </Grid>
                      <Grid item xs={12} pr={1} mb={6}>
                        <MKInput
                          variant="standard"
                          label="Your contact info*"
                          placeholder="How can I best get back to you?"
                          InputLabelProps={{ shrink: true }}
                          inputRef={contactRef}
                          fullWidth
                          multiline
                          rows={1}
                        />
                      </Grid>

                    </Grid>
                    <Grid
                      container
                      item
                      xs={12}
                      md={6}
                      justifyContent="flex-end"
                      textAlign="right"
                      ml="auto"
                    >
                      <MKButton variant="gradient" color={!sentMessage ? "info" : "success"} onClick={sendMessage} disabled={sentMessage}>
                        {!sentMessage ? "Send Message" : "Message Sent!"}
                      </MKButton>
                      {loading && <CircularProgress color="secondary" sx={{ ml: 3, }}></CircularProgress>}
                      {error && <MKTypography variant="body2" color="error" mb={2}>
                        Error sending contact message!
                      </MKTypography>
                      }
                    </Grid>
                  </MKBox>
                </MKBox>
              </Grid>
            </Grid>
          </MKBox>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default Contact;
