import React, { useState } from 'react';
import { Dialog, DialogContent, DialogContentText, DialogActions, Button } from '@mui/material';

const ControlsDialog = () => {
    const [open, setOpen] = useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div  >
            <Button onClick={handleClickOpen}>
                Controls
            </Button>
            <Dialog open={open} onClose={handleClose}>
                <DialogContent>
                    <DialogContentText>
                        <strong>Cmd + Enter</strong>: Sentence Autocomplete
                    </DialogContentText>
                    <DialogContentText>
                        <strong>Cmd + U</strong>: Rephrase
                    </DialogContentText>
                    <DialogContentText>
                        <strong>Cmd + . </strong>: Scroll through different rephrased options
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Close</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};

export default ControlsDialog;
