import * as React from 'react';
import TextField from '@mui/material/TextField';
import DialogContentText from '@mui/material/DialogContentText';
import axios from 'axios';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import InputLabel from '@mui/material/InputLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Container from "@mui/material/Container";
import CircularProgress from '@mui/material/CircularProgress';
import ControlsDialog from './ControlsDialog';

export default function FormDialog({ onClose }) {
    const [open, setOpen] = React.useState(true);
    const [toneValue, setToneValue] = React.useState('');
    const [topicValue, setTopicValue] = React.useState('');
    const [isDisabled, setIsDisabled] = React.useState(true);
    const [loading, setLoading] = React.useState(false);

    const handleClose = () => {
        setOpen(false);
    };

    const handleGenerate = async () => {
        try {
            setLoading(true)
            setIsDisabled(true)
            let data = JSON.stringify({
                content: "I'm writing a " + toneValue +
                    " text about " + topicValue +
                    ". Please provide me with a clearly thought out structure on how I should approach writing the text. Don't forget that I'm writing for a "
                    + toneValue + " purpose."
            });
            const response = await axios.post('https://ericnazarenus.de/generate-message', data, { headers: { "Content-Type": "application/json" } });
            if (response.status === 200) {
                onClose(response.data.message);
            } else {
                console.log('response not ok');
            }
        } catch (error) {
            console.log(error);
        }
        finally {
            setLoading(false);
            setIsDisabled(false);
        }

        setOpen(false);
    };

    const handleToneSelect = (event) => {
        const value = event.target.value;
        setToneValue(value);
    };
    const handleTopicSelect = (event) => {
        const value = event.target.value;
        setTopicValue(value);
        if (value === "") {
            setIsDisabled(true);
        } else {
            setIsDisabled(false);
        }

    };

    return (
        <Container>
            <Dialog
                open={open}
                PaperProps={{
                    component: 'form',
                    onSubmit: (event) => {
                        event.preventDefault();
                        handleGenerate(); // Call handleGenerate function
                    },
                }}
            >
                <DialogTitle>Set The Structure</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        To jump-start your writing process, describe what you are writing about.
                    </DialogContentText>

                    <Box component="form" >
                        <FormControl sx={{ m: 1, minWidth: 120, display: 'flex', flexDirection: 'row', flex: 1 }}>
                            <InputLabel htmlFor="tone">Tone</InputLabel>
                            <Select
                                native
                                value={toneValue}
                                onChange={handleToneSelect}
                                input={<OutlinedInput label="Tone" id="tone" />}
                            >
                                <option value={""}></option>
                                <option value={"creative"}>Creative</option>
                                <option value={"scientific"}>Scientific</option>
                                <option value={"comedic"}>Comedic</option>
                                <option value={"formal"}>Formal</option>
                                <option value={"informal"}>Informal</option>
                            </Select>
                            <TextField
                                id="topic"
                                label="Your Topic*"
                                value={topicValue}
                                onChange={handleTopicSelect}
                                sx={{ flex: 1, ml: 1 }}
                                multiline // Set to true for multiline input
                                maxRows={10}
                                InputProps={{
                                    style: { width: '100%', minHeight: '100%' }, // Set width to 100% and allow dynamic height
                                }}
                            />
                        </FormControl>
                    </Box>
                </DialogContent>
                <DialogActions>
                    <ControlsDialog />
                    <Button disabled={loading} onClick={handleClose}>No thanks</Button>
                    <Button disabled={isDisabled} type="submit">Generate Outline</Button>
                    {loading && <CircularProgress color="secondary" sx={{ ml: 3, }}></CircularProgress>}

                </DialogActions>
            </Dialog>
        </Container>
    );
}
