import React, { useState, useCallback, useEffect, useRef } from 'react'
import Quill from "quill"
import { useParams } from "react-router-dom"
import "quill/dist/quill.snow.css"
import "./styles.css"
import FormDialog from './FormDialog'
import axios from 'axios';
import { Box, LinearProgress } from '@mui/material'

const TOOLBAR_OPTIONS = [
    [{ header: [1, 2, 3, 4, 5, 6, false] }],
    [{ font: [] }],
    [{ list: "ordered" }, { list: "bullet" }],
    ["bold", "italic", "underline"],
    [{ color: [] }, { background: [] }],
    [{ script: "sub" }, { script: "super" }],
    [{ align: [] }],
    ["image", "blockquote", "code-block"],
    ["clean"],
]
function TextEditor() {
    const { id: documentID } = useParams();
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const quillRef = useRef(null);
    const [isLoading, setIsLoading] = useState(false);
    const [selected, setSelected] = useState()
    const [rephrasations, setRephrasations] = useState([]);

    useEffect(() => {
        setIsDialogOpen(true);

        const listener = async (event) => {
            const quill = quillRef.current;

            const selection = quill.getSelection();
            // Autocomplete Sentence on CMD + Enter
            if ((event.metaKey || event.ctrlKey) && event.key === 'Enter') {
                const text = selection ? quill.getText(0, selection.index) : quill.getText();
                let data = JSON.stringify({
                    content: "Please complete the last sentence of my text: " + text + ". Please make sure to only return your completion and not any of the text that I have already provided."
                });
                try {
                    setIsLoading(true);
                    const response = await axios.post('http://127.0.0.1:5000/complete-sentence', data, { headers: { "Content-Type": "application/json" } });
                    if (response.status === 200) {
                        const lastChar = quill.getText(selection.index - 1, 1);
                        let completedSentence = response.data.message.end_of_sentence;

                        // Check if we need to adjust the spacing
                        const needsSpaceBefore = !/\s/.test(lastChar);
                        const hasLeadingSpace = /^\s/.test(completedSentence);

                        if (needsSpaceBefore && !hasLeadingSpace) {
                            completedSentence = ' ' + completedSentence;
                        } else if (!needsSpaceBefore && hasLeadingSpace) {
                            completedSentence = completedSentence.trimStart();
                        }
                        quill.insertText(selection.index, completedSentence);
                    } else {
                        console.log('response not ok');
                    }
                } catch (error) {
                    console.error('API Error:', error);
                } finally {
                    setIsLoading(false);
                }
            }
            // Rephrase Sentence on CMD + U
            else if ((event.metaKey || event.ctrlKey) && event.key === 'u') {
                const text = selection ? quill.getText(selection.index, selection.length) : ""
                if (text !== "") {
                    let data = JSON.stringify({
                        content: "Please rephrase the text that I'm writing. I want you to give me 3 different versions: " + text
                    });
                    try {
                        setIsLoading(true);
                        const response = await axios.post('https://ericnazarenus.de/rephrase-sentence', data, { headers: { "Content-Type": "application/json" } });
                        if (response.status === 200) {
                            setRephrasations([response.data.message.rephrased_v1, response.data.message.rephrased_v2, response.data.message.rephrased_v3, text])
                            setSelected(selection)
                            quill.deleteText(selection.index, selection.length);
                            quill.insertText(selection.index, response.data.message.rephrased_v1);
                        } else {
                            console.log('response not ok');
                        }
                    } catch (error) {
                        console.error('API Error:', error);
                    } finally {
                        setIsLoading(false);
                    }
                }
            }
            else if ((event.metaKey || event.ctrlKey) && event.key === '.') {
                if (selected) {
                    let currentPhrase = rephrasations.shift();
                    rephrasations.push(currentPhrase);
                    quill.deleteText(selected.index, currentPhrase.length);
                    quill.insertText(selected.index, rephrasations[0]);
                }
            }
        };

        document.addEventListener('keydown', listener);

        return () => {
            document.removeEventListener('keydown', listener);
        };

    }, [selected, rephrasations]);

    const handleCloseDialog = (text_outline) => {
        console.log(documentID);
        formatAndInsertText(text_outline)
    };
    const formatAndInsertText = (text) => {
        if (!quillRef.current) return;

        const quill = quillRef.current;

        // Split text into lines
        const lines = text.outline.split('\n');
        let delta = [];

        // Regex patterns to identify headings and subheadings
        const heading1Pattern = /^\d+\.\s.*/; // e.g., I. Introduction
        const subheadingPattern = /^.\s.[A-Z]\.\s.*/; // e.g., A. Open with a humorous statement

        // Add Title 
        delta.push({ insert: text.title + '\n', attributes: { header: 1 } });

        lines.forEach(line => {
            if (heading1Pattern.test(line)) {
                delta.push({ insert: line + '\n', attributes: { header: 2 } });
            } else if (subheadingPattern.test(line)) {
                delta.push({ insert: line + '\n', attributes: { header: 5 } });
            } else {
                delta.push({ insert: line + '\n' });
            }
        });

        // Set the content of the Quill editor
        quill.setContents(delta);
    };

    const wrapperRef = useCallback((wrapper) => {
        if (wrapper == null) return;
        wrapper.innerHTML = ""
        const editor = document.createElement("div")
        wrapper.append(editor)
        const quill = new Quill(editor, {
            theme: 'snow',
            modules: { toolbar: TOOLBAR_OPTIONS },
        });
        quillRef.current = quill;
    }, [])

    return (
        <Box>
            <div>{isLoading && (
                <LinearProgress style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%'
                }}
                    sx={{ zIndex: 10 }} color="inherit" />
            )}</div>
            <div className='custom' ref={wrapperRef}>
                {isDialogOpen && (
                    <FormDialog onClose={handleCloseDialog} />
                )}
            </div>

        </Box>

    )
}


export default TextEditor